import React, {Component} from 'react';
import Navbar from './nav/navbar';
import './about.css';

export default class About extends Component {
    render(){
        return [
            <Navbar key="nav"/>,
            <section id="about" key="content">
                <h1><i>About Me</i></h1>
                <p>Hi my name is Trong and I am 19 years old. 
                I live in Wichita Kansas and currently I am a Sophomore 
                in college pursuing a bachelor in computer science. 
                My passion for coding started when I was 17 taking my 
                first programming class back in high school. I fell 
                in love with it, and I've been coding ever. My goal is to one day become a 
                full stack developer. I want to create amazing softwares that people can use.</p>
            </section>
        ]
    }
}


